<template>
  <div style="width:200px; height:200px; margin:auto; position:relative;">
    <svg viewBox="0 0 36 36" style="width:100%; height:100%;">
      <!-- 회색 배경 원 -->
      <path
        d="M18 2.0845
           a 15.9155 15.9155 0 0 1 0 31.831
           a 15.9155 15.9155 0 0 1 0 -31.831"
        stroke="#eee"
        stroke-width="2"
        fill="none"
      />
      <!-- 파란 게이지 -->
      <path
        d="M18 2.0845
           a 15.9155 15.9155 0 0 1 0 31.831
           a 15.9155 15.9155 0 0 1 0 -31.831"
        stroke="#007bff"
        stroke-width="2"
        fill="none"
        stroke-linecap="round"
        :stroke-dasharray="pageForm.fields.CURRENT_ALL_FG_POINT + ',100'"
      />
    </svg>

    <!-- 중앙 내용 -->
    <div
      style="
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%, -50%);
        text-align:center;
      "
    >
      <p style="margin:0; font-size:14px; color:#555;">공포지수</p>

      <div style="display:flex; justify-content:center; align-items:baseline;">
        <h1
          :style="{
            color: '#43a3fb',
            margin: '0',
            fontSize: '24px'
          }"
        >
          {{ pageForm.fields.CURRENT_ALL_FG_POINT }}
        </h1>
        <h1 style="margin:0 0 0 4px; font-size:18px; color:#555;">/100</h1>
      </div>

      <p style="margin:4px 0; font-size:12px; color:#777;">
        {{ pageForm.fields.CURRENT_DATE }}
      </p>

      <span>
        <h1
          style="
            font-size:16px;
            color:#333;
            margin:0;
            font-weight:bold;
          "
        >
          {{ pageForm.fields.CURRENT_ALL_STATUS }}
        </h1>
      </span>
    </div>
  </div>
</template>

<script>
import datas from '@/shared/coindata.json'

export default {
  data() {
    return {
      pageForm: {
        fields: {
          CURRENT_DATE: '',
          CURRENT_TIME: '',
          CURRENT_ALL_FG_POINT: 0,
          CURRENT_ALL_COMMENT: '',
          CURRENT_ALL_STATUS: ''
        }
      },
      findBox: {
        card_1: false, card_2: false, card_3: false, card_4: false, card_5: false
      },
      FG_tableData: [],
      json_data: datas,
      fg_loading: false,
      cardDescriptions: [
        { title: '매우공포 (< 20)', desc: '변동성이 크고 높은 거래량을 동반한 하락을 의미합니다. 패닉셀링이 이어지고 있습니다.' },
        { title: '공포 (< 40)', desc: '지수가 점진적으로 하락하고 있습니다. 거래량이 높아지고 있으며 단기 저점 가능성 있습니다.' },
        { title: '중립 (< 60)', desc: '심리적인 저항/지지를 받고 있으며 단기간 주가 중요 구간입니다.' },
        { title: '탐욕 (< 80)', desc: '지수가 점진적으로 상승, 거래량과 변동성 증가, 단기 고점 가능성 있습니다.' },
        { title: '매우탐욕 (< 100)', desc: '높은 거래량과 강한 변동성을 동반한 상승, 변동성 주의 필요.' }
      ]
    }
  },
  mounted() {
    this.fg_loading = true
    this.get_upbit_ubci()
  },
  methods: {
    async get_upbit_ubci() {
      const axios = require('axios')
      axios.get('https://definitely-handy-cow.ngrok-free.app/api/feargreed', {
        headers: { 'ngrok-skip-browser-warning': '69420' }
      })
      .then(response => {
        this.api_value = response.data
        let api_time_split = this.api_value.at.split('T')
        this.pageForm.fields.CURRENT_DATE = api_time_split[0]
        this.pageForm.fields.CURRENT_TIME = api_time_split[1].split(':')[0] + ":" + api_time_split[1].split(':')[1]
        this.pageForm.fields.CURRENT_ALL_FG_POINT = this.api_value.today.score.toFixed(1)
        this.pageForm.fields.CURRENT_ALL_STATUS = this.api_value.today.stage
        this.findDescriptionBox(Math.floor(this.pageForm.fields.CURRENT_ALL_FG_POINT))
        this.factoringHistoryTable(this.api_value.intv)
        this.fg_loading = false
      })
      .catch(() => {
        this.tmp_get_upbit_ubci()
      })
    },
    tmp_get_upbit_ubci() {
      this.api_value = this.json_data
      let api_time_split = this.api_value.at.split('T')
      this.pageForm.fields.CURRENT_DATE = api_time_split[0]
      this.pageForm.fields.CURRENT_TIME = api_time_split[1].split(':')[0] + ":" + api_time_split[1].split(':')[1]
      console.log(this.api_value)
      this.pageForm.fields.CURRENT_ALL_FG_POINT = this.api_value.today.score.toFixed(1)
      this.pageForm.fields.CURRENT_ALL_STATUS = this.api_value.today.stage
      this.findDescriptionBox(Math.floor(this.pageForm.fields.CURRENT_ALL_FG_POINT))
      this.factoringHistoryTable(this.api_value.intv)
      this.fg_loading = false
    },
    factoringHistoryTable(history_object) {
      const extractedData = history_object.map((item) => ({
        name: item.name,
        stage: item.stage,
        score: item.score
      }))
      const result_state = {}, result_score = {}
      extractedData.forEach((item, index) => {
        result_state[index + 1] = item.stage
        result_score[index + 1] = item.score
      })
      this.FG_tableData = [result_state, result_score]
    },
    findDescriptionBox(value) {
      for (let i = 1; i <= 5; i++) this.findBox[`card_${i}`] = false
      if (value <= 20) this.findBox.card_1 = true
      else if (value <= 40) this.findBox.card_2 = true
      else if (value <= 60) this.findBox.card_3 = true
      else if (value <= 80) this.findBox.card_4 = true
      else this.findBox.card_5 = true
    },
    indexMethod(index) {
      return index === 0 ? '점수' : '상태'
    }
  }
}
</script>

